import { KeyCode } from "@devexpress/utils/lib/utils/key";
import { DxDesktopMenuKeyboardStrategy, MenuKeyboardCommand } from "./desktop-menu-keyboard-strategy";
import { DropDownMenuKeyboardStrategyBase, DxMenuDropDownSubMenuKeyboardStrategy } from "./dropdown-menu-keyboard-strategy";
import { MenuItemSelector } from "./menu-keyboard-strategy";
export class DxVerticalMenuKeyboardStrategy extends DxDesktopMenuKeyboardStrategy {
    createKeyCodeToCommandMap() {
        return new Map([
            [KeyCode.Down, MenuKeyboardCommand.MoveToNextItem],
            [KeyCode.Up, MenuKeyboardCommand.MoveToPrevItem],
            [KeyCode.Right, MenuKeyboardCommand.OpenSubMenu | MenuKeyboardCommand.MoveToFirstItem],
            [KeyCode.Left, MenuKeyboardCommand.OpenSubMenu | MenuKeyboardCommand.MoveToLastItem],
            [KeyCode.Home, MenuKeyboardCommand.MoveToFirstItem],
            [KeyCode.End, MenuKeyboardCommand.MoveToLastItem],
            [KeyCode.Enter, MenuKeyboardCommand.Execute],
            [KeyCode.Space, MenuKeyboardCommand.Execute],
            [KeyCode.Tab, MenuKeyboardCommand.Leave]
        ]);
    }
    closeSubMenuAndMoveToNextItem() {
        this.requestSubMenuClose();
    }
    closeSubMenuAndMoveToPrevItem() {
        this.requestSubMenuClose();
    }
    createPopupStrategy(navigator, targetElement) {
        return new DxVerticalMenuDropDownRootMenuKeyboardStrategy(navigator, targetElement, MenuItemSelector, this, this.hasTransitContainer(targetElement));
    }
}
class DxVerticalMenuDropDownRootMenuKeyboardStrategy extends DropDownMenuKeyboardStrategyBase {
    createSubMenuStrategy(navigator, targetElement) {
        return new DxMenuDropDownSubMenuKeyboardStrategy(navigator, targetElement, this.menuItemSelector, this, this.hasTransitContainer(targetElement));
    }
    closeSelf() {
        this.parentStrategy.closeSubMenu();
        return true;
    }
}
