import { KeyCode } from "@devexpress/utils/lib/utils/key";
import { DxDesktopMenuKeyboardStrategy, MenuKeyboardCommand } from "./desktop-menu-keyboard-strategy";
export class DxHorizontalMenuKeyboardStrategy extends DxDesktopMenuKeyboardStrategy {
    createKeyCodeToCommandMap() {
        return new Map([
            [KeyCode.Right, MenuKeyboardCommand.MoveToNextItem],
            [KeyCode.Left, MenuKeyboardCommand.MoveToPrevItem],
            [KeyCode.Down, MenuKeyboardCommand.OpenSubMenu | MenuKeyboardCommand.MoveToFirstItem],
            [KeyCode.Up, MenuKeyboardCommand.OpenSubMenu | MenuKeyboardCommand.MoveToLastItem],
            [KeyCode.Home, MenuKeyboardCommand.MoveToFirstItem],
            [KeyCode.End, MenuKeyboardCommand.MoveToLastItem],
            [KeyCode.Enter, MenuKeyboardCommand.Execute],
            [KeyCode.Space, MenuKeyboardCommand.Execute],
            [KeyCode.Tab, MenuKeyboardCommand.Leave]
        ]);
    }
}
